.breadcrumbs {
    margin-bottom: 2rem;
    @include desktop {
        display: block;
        table,
        tbody,
        tr,
        td {
            display: block;
        }
    }
    a {
        color: $brand;
        text-decoration: none;
        margin: 0 0.5rem;
        &:hover {
            text-decoration: underline;
        }
        &:first-of-type {
            margin-left: -0.75rem;
        }
    }
    
    tr:first-child {
        display: none;
        @include desktop {
            display: table-row;
        }
    }

}

