/*
=========================
SEARCH FILTERS
=========================
*/
.breadcrumbs tr:nth-child(2) td {
    display: flex;

    select {
        border: 1px solid $medium-gray;
    }

}

/*
=========================
SORTING FILTERS
=========================
*/
.breadcrumbs + table {
    select {
        border: 1px solid $medium-gray;
    }
}

.search_results_section {
    table:first-child {
        width: 100%;
        margin-bottom: 1rem;
    }
}

.vCSS_breadcrumb_td {
    display: none;
    border-bottom: 1px solid $medium-gray;
    padding-bottom: 0.5rem;
    @include tablet {
        display: table-cell;
    }

    a {
        margin-left: 0.5rem;
        margin-right: 0.5rem;
        color: $brand;
        text-decoration: none;
        &:hover {
            text-decoration: underline;
        }
    }

    a:first-of-type {
        margin-left: -0.5rem;
    }
    
    br {
        display: none;
    }

}