.site-width {
    width: calc(100% - 2rem);
    max-width: 1080px;
    margin: 0 auto;
}

.pad-x {
    padding-left: 1rem;
    padding-right: 1rem;
    &--more {
        padding-left: 2rem;
        padding-right: 2rem;
    }
    &--most {
        padding-left: 3rem;
        padding-right: 3rem;        
    }
    &--none {
        padding-left: 0;
        padding-right: 0;
    }
}
