.mobile-only {
    @include desktop {
        display: none;
    }
}

.visually-hidden {
    border: 0;
    clip: rect(0 0 0 0);
    height: 1px;
    margin: -1px;
    overflow: hidden;
    padding: 0;
    position: absolute;
    width: 1px;
    white-space: nowrap; /* 1 */
}

/*
====================
TYPOGRAPHY
====================
*/
.has-text-center {
    text-align: center;
}

.font-shrink {
    font-size: 0.85em;
}

.editSearchLink,
.editProductLink {
    position: fixed;
    z-index: 1000;
    bottom: 1rem;
    left: 1rem;
}



.ui-menu,
.ui-menu-item {
    max-width: 100% !important;
}

// make sure autocomplete is always at the top
.ui-widget-content {
    z-index: 999 !important;
}